body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background: #f7f9fc;
  color: #333;
}

.app {
  padding: 20px;
  max-width: 1200px;
  margin: auto;
}

header {
  text-align: center;
  margin-bottom: 30px;
}

.upload-section {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.upload-box {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #ddd;
  flex: 1;
  min-width: 300px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
}

.upload-box input {
  margin-top: 10px;
  display: block;
}

.upload-box textarea {
  width: 100%;
  height: 150px;
  margin-top: 10px;
  padding: 10px;
  font-family: monospace;
  resize: vertical;
}

.preview-image {
  margin-top: 10px;
  width: 100%;
  max-height: 200px;
  object-fit: contain;
  border: 1px solid #ccc;
}

.controls {
  text-align: center;
  margin-top: 20px;
}

.controls button {
  background: #007bff;
  color: white;
  font-size: 16px;
  margin: 0 10px;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.controls button:hover {
  background: #0056b3;
}

.result {
  margin-top: 30px;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #ddd;
}

.line {
  margin-bottom: 12px;
  padding: 10px;
  border-left: 4px solid transparent;
}

.match {
  background: #e8fbe8;
  border-left-color: #28a745;
}

.diff {
  background: #fff3cd;
  border-left-color: #ffc107;
}

.corrected {
  color: #155724;
  display: block;
}

.original {
  color: #721c24;
  text-decoration: line-through;
  display: block;
}

.loading {
  text-align: center;
  font-weight: bold;
  color: #007bff;
  margin-top: 20px;
}

footer {
  text-align: center;
  margin-top: 40px;
  font-size: 14px;
  color: #888;
}

